.mainslide .swiper-wrapper {width: 100%;height:91vh;}
.mainslide .swiper-slide {text-align: center;font-size: 18px;background: #fff;
display: flex;justify-content: center;align-items: center;}
.mainslide .slidebox{position:relative; width:100%; height:100%;}
.slidebox img{width:100%; display:absolute;top:0; left:0;}
.slideboxinner{position:absolute; bottom:35%; right:8%;text-align:left;}
.slideboxinner h6{color:#fff;font-size:50px;font-weight:300; margin:3% 0;}
.slideboxinner p{color:#faedcd;margin:2% 0;}
.slideboxinner button{border-color:#CCD5AE; color:#fff; margin:4% 0;}
.slideboxinner button:hover{background-color:#CCD5AE; border-color:#CCD5AE}
.swiper-slide img {display: block;width: 100%; height: 100%;object-fit: cover;}
.swiper {margin-left: auto;margin-right: auto;}
.swiper .swiper-button-prev{color:#CCD5AE;}
.swiper .swiper-button-next{color:#CCD5AE;}
.swiper .swiper-pagination-bullet-active{background-color:#333;}





.four_part{margin:10% auto;}     
.item{overflow:hidden; padding:0; margin:1%; width:48%; position:relative;}
.item img{transition:all .5s; }
.item:hover img{transform:scale(1.1); cursor:pointer;}
.h_2{position:absolute; top:50%; left:50%; transform:translate(-50%,-50%); color:#FFF; font-weight:400;}





.arrivalbox{width:100%;background-color:#E9EDC9; margin:10% auto;}
.arrivalbox h3{padding-top:80px; font-size:35px;}
.arrivalbox button{border-color: #CCD5AE; margin:30px 10px;}
.arrivalbox button:hover{background-color:#CCD5AE; border-color:#CCD5AE}
.arrivalbox .nav-link {background-color:#FFF; margin:10px 0; padding:5px 0; border-radius:10px;}
.arrivalbox .nav-link:hover{box-shadow:1px 1px 15px #999;}
.arrivalbox .nav-link img{width:60%; margin:0 auto;}
.arrivalbox .nav-link h4{margin:10px 0; font-size:18px; height:50px;}
.arrivalbox .nav-link p{margin:10px 0;font-size:14px; color:#888;}
.arrivalbox .swiper-slide{margin-bottom:50px;}
.arrivalbox .swiper-wrapper.sw-center{justify-content:center;}
.arrivalbox .swiper-pagination-bullet-active{background-color:#333;}
.arrivalbtn{background-color:#CCD5AE; color:#F4F4F4;}






.timebox{width:100%; margin:5% auto;}
.clockbox{display:flex; flex-flow:row wrap; width:80%; margin:0 auto;justify-content:space-around;align-items:center; padding:20px;background-color:#F4F4F4 }
.leftclock{display:flex; flex-flow:row nowrap;justify-content:space-between;align-items:center;height:50px;}
.leftclock p{flex:0 0 100px; margin-bottom:0; font-weight:500;}
.leftclock .timehour span{border: 1.5px solid #ccd5ae; padding:5px; margin:0 10px; font-weight:500;}

.rightclock{display:flex; flex-flow:row nowrap;justify-content:space-around; align-items:center; height:50px;}
.rightclock p{margin:0 3%; cursor:pointer; font-weight:500;}
.rightclock p:hover{font-weight:600;}
.clock{position:relative; background-color:#F4F4F4; text-align:left;}
.clock h2{position:absolute; top:20%; right:10%; text-align:left; font-weight:500; }
.clock img{margin:15px 0}
.clock>p{position:absolute; top:15%; right:10%; color:#888;}
.right h5{margin:7% 0; font-size:18px;}
.right h6{color:#888;}







.enlarge{width:100%; height:650px; margin:5% 0;background-color:#ccd5ae;}
.enlarge_top{background-image: url('../public/img/largebox.jpg'); height:400px;background-size:cover;background-position:bottom; overflow:hidden; position:sticky; top:0;}
.enlarge_bottom_box{position:relative; z-index:5; background-color:#CCD5AE;width:100%;}
.enlarge_bottom h6{font-size:20px;}
.enlarge_bottom p{color:#888; font-size:18px;}
.enlarge_iconbox{border-radius:50%; background-color:#fff;
  width:80px; height:80px; margin:8% auto;
  display:grid; place-items:center;}
.enlarge_iconbox img{width:40%;}
.section{width:100%; height:50px; background:#CCD5AE; position:relative; z-index:5;}





.blog{margin:10% auto;}
.blog .card{margin:2%; border-left:1px solid rgba(0,0,0,0.175) !important}
.blog h3{margin:10% auto 5%; font-size:35px;}
.blog p{margin:2% auto; font-size:20px;}
.blog .btn{border-color:#CCD5AE;}
.blog .btn:hover{background-color:#CCD5AE;}
.card-body .card-text{font-size:14px; color:#888}





/*-모바일사이즈----------------------------------------------- */
