    .mainslide .slideboxinner{position:absolute; bottom:15%;right:0; text-align:right;}
    .mainslide .slideboxinner h6{color:#fff;font-size:30px;font-weight:300; margin:25% 5%;}
    .mainslide .slideboxinner p{color:#FEFAE0; margin:2% auto; width:85%;}
    .mainslide .swiper-wrapper {width: 100%;height:80vh; object-fit: cover;}
    .mainslide .slideboxinner button{border-color:#FEFAE0; color:#fff; margin:10%;}
    .mainslide .slideboxinner button:hover{background-color:#FEFAE0; border-color:#FEFAE0; color:#333;}
  
  
    .item{padding:0; margin:1%; width:98%;}
  
  
    .arrivalbox .nav-link img{margin:10px auto;}
    .arrivalbox .nav-link h4{margin:10px 0; font-size:15px; height:60px;}
    .arrivalbox .nav-link p{margin:10px 0;font-size:13px; color:#888;}
  
  
    .clockbox{display:flex; flex-flow:row wrap; width:90%; margin:0 auto;justify-content:space-between;align-items:center;background-color:#F4F4F4 }
    .leftclock{display:flex; flex-flow:row nowrap; justify-content:space-around;align-items:flex-start;}
    .leftclock .timehour span{font-size:10px;}
    .rightclock{align-self:flex-end; display:flex; flex-flow:row nowrap;justify-content:space-around; align-items:flex-end; margin:0 auto;}
    .rightclock p{font-size:13px;}
  
    .enlarge_bottom h6{font-size:15px; padding-top:15px;}
    .enlarge_bottom p{color:#666; font-size:12px;}
  
  
    .blog h3{margin:15% auto 5%; font-size:35px;}
    .blog p{margin:10% auto; font-size:15px;}  
    }
    
    
    
    /*-태블릿사이즈----------------------------------------------- */
