    .mainslide .slideboxinner button:hover{background-color:#FEFAE0; border-color:#FEFAE0}
  
  
    .item{padding:0; margin:1%; width:98%;}
  
  
    .arrivalbox .nav-link img{margin:10px auto;}
    .arrivalbox .nav-link h4{margin:10px 0; font-size:18px; height:80px;}
    .arrivalbox .nav-link p{margin:10px 0;font-size:14px; color:#888;}
  
  
  
    .clockbox{display:flex; flex-flow:row wrap; width:80%; margin:0 auto;justify-content:space-around;align-items:center;background-color:#F4F4F4 }
    .leftclock{display:flex; flex-flow:row nowrap;justify-content:space-around;align-items:flex-start;}
    .leftclock .timehour span{font-size:15px;}
    .rightclock{align-self:flex-end; display:flex; flex-flow:row nowrap;justify-content:space-around; align-items:flex-end;}
    .clock{text-align:center;}
    .clock h2{position:absolute; top:25%; right:10%; text-align:left; font-weight:500;}
  
  
  
    .enlarge_bottom h6{font-size:18px; padding-top:20px;}
    .enlarge_bottom p{color:#666; font-size:13px;}
  
  
    .blog h3{margin:15% auto 5%; font-size:30px;}
    .blog p{margin:10% auto; font-size:15px;}
    .card-group{display:flex; flex-flow:column wrap;}
    .card-body .card-text{font-size:15px;}
    }
    